@import "variables";

.input-container{
    position: relative;
}

label{
    display: inline-block;
    margin-bottom: $xs;
}

input, textarea{
    display: inline-block;
    width: 100%;
    padding: $m;
    color: var(--text-color);
    margin-bottom: $l;
    border-radius: 0;

    &::placeholder{
        color: var(--text-color);
        opacity: .3;
    }

    &:focus{
        border: 1px solid transparent !important;
        border-image: $theme !important;
        border-image-slice: 1 !important;
        outline: none;

        & + .input-icon--invalid,
        & + .input-icon--valid{
            display: none !important;
        }
    }

    &:invalid{
        border: 1px dashed red;

        & ~ .input-icon--invalid{
            display: inline;
        }
    }

    &:not(:focus):valid:not(input[type="submit"]){
        border: 1px solid transparent;
        border-image: $akq-green-gradient;
        border-image-slice: 1;

        & ~ .input-icon--valid{
            display: inline;
        }
    }

    &[type="checkbox"] + label,
    &[type="radio"] + label,
    &:placeholder-shown{

        & ~ .input-icon--invalid,
        & ~ .input-icon--valid{
            display: none;
        }
    }
}

input[type="checkbox"],
input[type="radio"]{
    display: none;

    & + label{
        position: relative;
        width: 100%;
        margin-bottom: $l;
        padding: $m calc(#{$m} + #{$l} + #{$m}) $m $m;

        &::after{
            content: "";
            background: var(--text-color);
            height: $l;
            width: $l;
            position: absolute;
            top: 50%;
            right: $m;
            transform: translateY(-50%);
            mask-size: $l;
        }
    }

    &:checked + label{
        border: 1px solid transparent;
        border-image: $theme;
        border-image-slice: 1;
        outline: none;

        &::after{
            background-image: $theme;
            filter: invert(0);
        }
    }
}

input[type="checkbox"]{
    & + label::after{
        mask-image: url("https://api.iconify.design/mdi/checkbox-blank-outline.svg");
    }

    &:checked + label::after{
        mask-image: url("https://api.iconify.design/mdi/checkbox-marked.svg");
    }
}

input[type="radio"]{
    & + label::after{
        mask-image: url("https://api.iconify.design/mdi/radiobox-blank.svg");
    }

    &:checked + label::after{
        mask-image: url("https://api.iconify.design/mdi/radiobox-marked.svg");
    }
}

.input-icon--invalid,
.input-icon--valid{
    display: none;
    position: absolute;
    right: $xs;
    bottom: $xl;
    transform: translateY(-50%);
}

.input-icon--invalid{
    color: $akq-red;
}

.input-icon--valid{
    color: $akq-green;
}

form:valid input[type="submit"]{
    background-image: $theme;
    opacity: 1;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 500;
}

input[type="submit"]{
    opacity: .3;
    cursor: not-allowed;
    -webkit-appearance: none;
}

@media (prefers-color-scheme: dark) {
    input,
    textarea{
        background-color: $darker-gray;
        border: 1px solid $dark-gray;

        &[type="checkbox"] + label,
        &[type="radio"] + label,
        &:placeholder-shown{
            background-color: $darker-gray;
            border: 1px solid $dark-gray;
        }
    }
}

@media (prefers-color-scheme: light) {
    input,
    textarea{
        background-color: $near-white;
        border: 1px solid $light-gray;

        &[type="checkbox"] + label,
        &[type="radio"] + label,
        &:placeholder-shown{
            background-color: $near-white;
            border: 1px solid $light-gray;
        }
    }
}   