@import "variables";

svg.iconify{
    &[data-iconsize="m"]{
        // 24x24
        height: $l;
        width: $l;
    }
    
    &[data-iconsize="s"]{
        // 16x16
        height: $m;
        width: $m;
    }
    
    &[data-iconsize="l"]{
        // 48x48
        height: $xxl;
        width: $xxl;
    }
}