@import "../../stylesheets/partials/variables";
@import "../../stylesheets/partials/animations";

header{
    position: relative;
    min-height: 66vh;
    display: grid;
    place-content: center;

    &::after{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        max-height: 100%;
        width: 100%;
        aspect-ratio: 16 / 9;
        background-color: var(--foreground-color-1);
        mask-image: url("../images/masks/bg-wave-top.svg");
        mask-size: 100%;
        mask-position: bottom;
        mask-repeat: no-repeat;
        z-index: -2;
    }

    .h2{
        text-shadow: none;
    }

    .blob{
        background-color: var(--foreground-color-2);
        height: clamp(24rem, calc(2rem + 30vw), 40rem);
        aspect-ratio: 1;
        position: absolute;
        z-index: -1;
        mask-image: url("../images/masks/blob-image-2.svg");

        &.upper-left{
            top: 0;
            left: 0;
            transform: translate(-40%, -50%);
        }

        &.lower-right{
            bottom: 0;
            right: 0;
            transform: translate(50%, 50%);
        }
    }

    #portrait{
        position: relative;

        &::before,
        &::after{
            content: "";
            background-color: var(--foreground-color-2);
            position: absolute;
            height: 50%;
            width: 50%;
            mask-size: 100%;
            z-index: -1;
            mask-repeat: no-repeat;
            mask-size: 84%;
        }

        &::before{
            bottom: 0%;
            left: 0;
            mask-image: url("../images/masks/dots-square-animated.svg");
        }

        &::after{
            top: 0;
            right: 0;
            mask-image: url("../images/masks/dots-round-animated.svg");
        }

        img{
            background-image: $theme;
            mask-image: url("../images/masks/blob-image-2.svg");
            mask-size: 102%;
            mask-repeat: no-repeat;
            mask-position: 50% 120%;
        }
    }
}

@media screen and (max-width: 960px){
    header{
        text-align: center;
    }
}