@import "../../stylesheets/partials/variables";
@import "../../stylesheets/partials/helpers";

dl{
    display: grid;
    grid-template-columns: repeat( auto-fit, minmax(10rem, 1fr) );
    grid-gap: $l;

    div{
        aspect-ratio: 1;
        position: relative;

        &::after{
            content: "";
            display: inline-block;
            background-color: var(--foreground-color-2);
            height: 100%;
            width: 100%;
            mask-image: url("../../images/masks/dots-round-animated.svg");
            mask-size: 100%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            opacity: 0;
            will-change: opacity;
            transition: opacity $transition;
        }

        &:hover::after{
            opacity: 1;
        }

        dt{
            order: 2;
        }
    }
}
