@keyframes rotate {
    0%{
        transform: rotate(0deg);
    }

    100%{
        transform: rotate(360deg);
    }
}

@keyframes pulse {
    0%{
        box-shadow: $shadow-1, 0 0 0 0 rgba($akq-red, .95);
    }

    100%{
        box-shadow: $shadow-1, 0 0 0 $xs rgba($akq-red, 0);
    }
}

@keyframes color {
    0%{
        background-color: $akq-lightgreen;
    }

    12.5%{
        background-color: $akq-green;
    }

    25%{
        background-color: $akq-darkgreen;
    }

    37.5%{
        background-color: $akq-blue;
    }

    50%{
        background-color: $akq-purple;
    }

    62.5%{
        background-color: $akq-red;
    }

    75%{
        background-color: $akq-orange;
    }

    87.5%{
        background-color: $akq-yellow;
    }

    100%{
        background-color: $akq-red;
    }
}
