/* -------------------- Sizes -------------------- */

$xxxs: .125rem;     // 2px
$xxs: .25rem;       // 4px
$xs: .5rem;         // 8px
$s: .75rem;         // 12px
$m: 1rem;           // 16px
$l: 1.5rem;         // 24px
$xl: 2rem;          // 32px
$xxl: 3rem;         // 48px
$xxxl: 5rem;        // 80px
$xxxxl: 10rem;      // 160px

$xsizes: (
    "0": 0,
    "xxxs": $xxxs,
    "xxs": $xxs,
    "xs": $xs,
    "s": $s,
    "m": $m,
    "l": $l,
    "xl": $xl,
    "xxl": $xxl,
    "xxxl": $xxxl,
    "xxxxl": $xxxxl,
);

/* -------------------- Colors -------------------- */

/* Semantic */
$warning: #FFCC00;
$error: #FF0000;
$xsuccess: #00CC22;

/* Brand */
$akq-lightgreen: #A8C322;
$akq-green:#32A546;
$akq-darkgreen: #05806E;
$akq-blue: #17518E;
$akq-purple: #C13B70;
$akq-red: #E7324C;
$akq-orange: #F3920F;
$akq-yellow: #FFCC00;

/* Grayscale */
$white: #fff;
$near-white: #fbfbfb;
$lightest-gray: #f7f7f7;
$lighter-gray: #f2f2f2;
$light-gray: #ccc;
$medium-gray: #666;
$dark-gray: #313131;
$darker-gray: #212121;
$darkest-gray: #111;
$near-black: #0a0a0a;
$black: #000;

$colors: (
    "warning": $warning,
    "error": $error,
    "success": $xsuccess,
    "akq-lightgreen": $akq-lightgreen,
    "akq-green":$akq-green,
    "akq-darkgreen": $akq-darkgreen,
    "akq-blue": $akq-blue,
    "akq-purple": $akq-purple,
    "akq-red": $akq-red,
    "akq-orange": $akq-orange,
    "akq-yellow": $akq-yellow,
    "white": $white,
    "near-white": $near-white,
    "lightest-gray": $lightest-gray,
    "lighter-gray": $lighter-gray,
    "light-gray": $light-gray,
    "medium-gray": $medium-gray,
    "dark-gray": $dark-gray,
    "darker-gray": $darker-gray,
    "darkest-gray": $darkest-gray,
    "near-black": $near-black,
    "black": $black,
);

/* Gradients */
$theme: linear-gradient(135deg, $akq-red, $akq-purple);
$akq-gradient: linear-gradient(135deg, $akq-lightgreen, $akq-green, $akq-darkgreen, $akq-blue, $akq-purple, $akq-red, $akq-orange, $akq-yellow);
$akq-green-gradient: linear-gradient(135deg, $akq-lightgreen, $akq-green);

$gradients: (
    "theme": $theme,
    "akq-gradient": $akq-gradient,
);

/* -------------------- Misc -------------------- */

/* Shadow */
$shadow-1: 0 1px $xxxs rgba($black, .3);
$shadow-2: 0 $xxxs $xs rgba($black, .1);
$shadow-3: 0 $xs $xs rgba($black, .1);
$shadow-4: 0 $xs $m rgba($black, .1);
$shadow-5: 0 $m $xl rgba($black, .1);

/* Animation */
$transition: .2s ease-in-out;