@media (prefers-reduced-motion: reduce) {
    // Prevent smooth page scrolling
    :root{
        scroll-behavior: auto;
    }

    // Replace animated dots static dots
    header #portrait{

        &::before{
            mask-image: url("../../images/masks/dots-square.svg") !important;
        }

        &::after{
            mask-image: url("../../images/masks/dots-round.svg") !important;
        }
    }

    // Replace animated dots static dots
    dl div::after{
        mask-image: url("../../images/masks/dots-round.svg") !important;
    }

    // Prevent button pulse animation
    a[class^="btn"],
    button[class^="btn"] {
        
        &.btn--pulse{
            animation: none !important;
        }
    }
}
