@import "../../stylesheets/partials/variables";

footer#kontakt{
    position: relative;

    &::after{
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        max-height: 100%;
        width: 100%;
        aspect-ratio: 16 / 9;
        background-color: var(--foreground-color-1);
        mask-image: url("../../images/masks/bg-wave-bottom.svg");
        mask-size: 100%;
        mask-position: top;
        mask-repeat: no-repeat;
        z-index: -2;
    }

    #socials{
        display: flex;
        gap: $m;
        margin-top: $xxl;
    }
    
    #legal{
        margin-top: $xxxl;
    
        nav#footer-nav{
            display: inline-block;
    
            ul{
    
                li a + a{
                    margin-left: $xl;
                }
            }
        }
    }
}