/* -------------------- Basic -------------------- */

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    ::selection{
        background-color: $akq-red;
    }
}

:root{
    scroll-behavior: smooth;
    overflow-x: hidden;
    scrollbar-color: $akq-red var(--background-color);
}

::-webkit-scrollbar {
    width: $s;
    background: transparent;
}

::-webkit-scrollbar-thumb {
    background: $akq-red;
    border-radius: $m;
}


body{ 
    background-color: var(--background-color);
    overflow-x: hidden;
}

main{
    position: relative;
    padding: clamp(.5rem, calc(.25rem + 2vw), 2rem);

    &::before{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        aspect-ratio: 16 / 9;
        background-color: var(--foreground-color-1);
        mask-image: url("../../images/masks/bg-wave.svg");
        mask-repeat: no-repeat;
        mask-position: center;
        mask-size: cover;
        z-index: -3;
    }

    article{
        padding: clamp($xxl, 5vw, $xxxl) 0;
        opacity: 0;
        transition: opacity 1.5s;
    }
}

footer{
    padding: clamp($xxl, 5vw, $xxxxl) $m;
}

img{
    display: block;
    max-width: 100%;
    height: auto;
    margin: 0 auto;
}

.card{
    position: relative;
    background: var(--foreground-color-2);
    border: 1px solid var(--foreground-color-3);
    border-radius: clamp(.5rem, calc(.5rem + 4vw), 2rem);
    padding: clamp(.5rem, calc(.5rem + 4vw), 3rem);
    overflow: hidden;

    a[class^="btn"].hide-on-touch:not(.hide-on-mouse){
        position: absolute;
        top: 0;
        left: 0;
        will-change: transform, opacity;
        opacity: 0;
        z-index: 9999;
        cursor: none;
    }
}

.tags{
    display: flex;
    align-items: center;
    gap: $m;

    .tag{
        padding: $xs $s;
        background-color: var(--foreground-color-3);
        border: 1px solid var(--text-color);
        border-radius: $xs;
        font-size: 80%;
        line-height: 1;
    }
}

svg{
    display: block;
    max-width: 100%;

    &.smartphone{
        max-width: 320px;
        margin: 0 auto;
        mask-image: url("../images/masks/smartphone-mask.svg");
        backdrop-filter: blur($xs);
    }

    &.tablet{
        max-width: 320px;
        margin: 0 auto;
        mask-image: url("../images/masks/tablet-mask.svg");
        backdrop-filter: blur($xs);
    }

    &.display{
        max-width: 640px;
        margin: 0 auto;
        mask-image: url("../images/masks/display-mask.svg");
        backdrop-filter: blur($xs);
    }
}

@media (prefers-color-scheme: light) {
    svg .device-body{
        fill: rgba($darkest-gray, .8);
    }
}   
  
@media (prefers-color-scheme: dark) {
    svg .device-body{
        fill: rgba($lightest-gray, .4);
    }
}