.wysiwyg{

    h3{
        margin: $xl 0 $l 0;
    }

    ul{
        list-style-position: inside;
        display: flex;
        flex-direction: column;
        gap: $m;
        padding-left: $l;
        margin-bottom: $xl;
    
        li{
            display: flex;
            align-items: center;
            gap: $m;
    
            &::before{
                content: "";
                display: inline-block;
                background-image: $theme;
                height: $s;
                width: $s;
                mask-image: url("https://api.iconify.design/mdi/square.svg");
            }
        }
    }

    img{
        width: 100%;
        max-width: 640px;
        margin: calc(min(8vw, 5rem)) auto;
        border-radius: $xs;
    }

    .btn{
        margin: 0;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
    }

    & + .wysiwyg{
        margin: $l 0;
    }
}