@import "../../stylesheets/partials/variables";

ul#tool-list{
    flex-wrap: wrap;

    li{

        img{
            height: $xxl;
            filter: drop-shadow($shadow-3);
        }
    }
}

ul#workflow{
    li{
        flex-grow: 1;
        line-height: 1;

        &:nth-child(2n) svg.iconify{
            color: $akq-red;
        }

        &:last-of-type{
            display: none;
        }
    }
}

@media screen and (max-width: 960px){
    ul#workflow{
        li{
            &:nth-child(2n) svg.iconify{
                transform: rotate(90deg);
            }
        }
    }
}