@import "variables";

html{
    font-family: Poppins, sans-serif;
    font-weight: 500;
    word-spacing: $xxs;
    color: var(--text-color);
}

*{
    font-size: 100%;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, dt{
    text-transform: uppercase;
    line-height: 1.2;
    font-weight: 600;
}

h1, .h1{
    font-size: clamp(1.75rem, calc(2rem + 2vw), 4.5rem);
    margin-bottom: $xxl;
    text-shadow: $xxxs $xxxs var(--background-color), $shadow-2, $xxs $xxs $akq-red, $shadow-2;
}

h2, .h2{
    font-size: clamp(1.5rem, calc(1.5rem + 2vw), 3rem);
    margin: clamp($xxl, calc(2rem + 2vw), $xxxl) 0 clamp($xl, calc(1rem + 1vw), $xxl) 0;
    text-shadow: 1px 1px var(--background-color), $shadow-2, $xxxs $xxxs $akq-red, $shadow-2;
}

h3, .h3{
    font-size: clamp(1rem, calc(1rem + 2vw), 2rem);
    margin-bottom: $m;
    text-shadow: none !important;
}

h4, .h4{
    font-size: clamp(1.25rem, calc(1rem + 1.5vw), 1.5rem);
    margin-bottom: $m;
    text-shadow: none !important;
}

p, figcaption, dt, dd, label, input, textarea, li{
    font-size: clamp(1rem, calc(1rem + 1vw), 1.25rem);
    line-height: 1.5;
    font-weight: 300;
}

p{
    margin-bottom: $xl;
}

ul{
    list-style: none;
}

dl{
    dt{
        line-height: 1;
    }
}

a{
    color: $akq-red;
    will-change: color;
    transition: color $transition;

    &:hover{
        color: var(--text-color);
    }
}

small{
    font-size: 80%;
}

strong{
    font-weight: 600;
}

@media screen and (max-width: 960px){
    h1, .h1, h2, .h2, h3, .h3, h4, .h4, dt{
        word-wrap: break-word;
        hyphens: auto;
    }
}