@media not all and (min-resolution:.001dpcm) {
    @supports (-webkit-appearance:none) and (stroke-color:transparent) {
        header{
            #portrait img,
            .blob{
                mask-image: url("../../images/masks/blob-image.svg") !important;

            }
        }

        ul#project-list li .project-image::after{
            mask-image: url("../../images/masks/blob-image.svg") !important;
        }
    }
}