@media (prefers-color-scheme: light) {
    :root{
        --background-color: #{$white};
        --foreground-color-1: #{$near-white};
        --foreground-color-2: #{$lightest-gray};
        --foreground-color-3: #{$lighter-gray};
        --text-color: #{$darkest-gray};

        ::selection {
            color: #fff;
        }
    }
}   
  
@media (prefers-color-scheme: dark) {
    :root{
        --background-color: #{$near-black};
        --foreground-color-1: #{$darkest-gray};
        --foreground-color-2: #{$darker-gray};
        --foreground-color-3: #{$dark-gray};
        --text-color: #{$lightest-gray};
    }

    .logo{
        filter: invert(.95);
    }
}