@import "variables";

a[class^="btn"],
button[class^="btn"] {
    background-image: $theme;
    color: $white;
    border: none;
    border-radius: $xxxxl;
    padding: $s $m;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 1.125rem;
    font-weight: 500;
    line-height: 1.2;
    display: inline-flex;
    gap: $xs;
    align-items: center;
    cursor: pointer;
    position: relative;
    box-shadow: $shadow-1;

    &:hover,
    &:focus{
        opacity: .9;
    }

    & + a[class^="btn"],
    & + button[class^="btn"]{
        margin-left: $xs;
    }

    &.btn--light{
        background-image: none;
        background-color: $near-white;
        color: #000;
        mix-blend-mode: screen;
    }

    &.btn--dark{
        background-image: none;
        background: $darker-gray;
        color: $near-white;
    }

    &.btn--ghost{
        background-image: none;
        color: var(--text-color);
        box-shadow: none;
        border: $xxxs solid currentColor;
    }

    &.btn--pulse{
        will-change: box-shadow;
        animation: pulse 1.8s infinite;

        &:hover{
            animation: none;
        }
    }

    &.btn--link{
        background: none;
        box-shadow: none;
    }

    &.btn--rounded{
        aspect-ratio: 1;
        border-radius: $xxxxl;
        text-align: center;

        svg.iconify{
            margin-left: 0;
        }
    }

    &[href=""]{
        filter: grayscale(1);
        opacity: .4;
        cursor: not-allowed;
        box-shadow: none;
    }

    &[download]{
        will-change: box-shadow;
        animation: pulse 1.8s infinite;

        &::after{
            content: "";
            display: inline-block;
            height: $l;
            width: $l;
            background-color: $white;
            mask-image: url("https://api.iconify.design/ic/baseline-download.svg");
            mask-size: 100%;
        }
    }

    svg.iconify{
        fill: $white;
    }
}