@import "../../stylesheets/partials/variables";

ul#sideproject-list{
    grid-template-columns: 1fr 1fr;

    li.card{

        &:last-of-type:nth-child(odd){
            grid-template-rows: 1fr;
            grid-template-columns: 1fr 1fr;
            grid-column: 1 / 3;
            display: flex;
            align-items: center;
        }

        img{
            place-self: center;
            max-height: 320px;
        }
    }
}

@media screen and (max-width: 960px){
    ul#sideproject-list{
        grid-template-columns: 1fr;

        li.card:last-of-type:nth-child(odd){
            grid-column-start: 1;
            grid-column-end: 2;
            grid-template-rows: auto 1fr auto;
            grid-template-columns: 1fr;
            display: block;
        }
    }
}
