@import "../../stylesheets/partials/variables";

nav:not(#footer-nav){
    position: fixed;
    top: 0;
    z-index: 99999;
    width: 100vw;
    backdrop-filter: blur($xs);
    box-shadow: $shadow-3;

    // Desktop Nav
    &#primary-nav{
        padding: 0 $xs;
    
        ul{
            display: flex;
            align-items: center;
    
            li{
                flex-grow: 1;
                text-align: center;
    
                &:first-of-type a{
                    float: left;
                    will-change: opacity;
                    transition: opacity $transition;
    
                    &:hover{
                        opacity: .9;
                    }
                }
    
                a:not(.btn){
                    text-decoration: none;
                    text-transform: uppercase;
                    font-size: 1.125rem;
                    font-weight: 500;
                    color: var(--text-color);
                    line-height: 1;
                    padding: $s $m;
                    position: relative;
    
                    &.nav-item{
                        &::after{
                            content: "";
                            display: inline-block;
                            background-image: $theme;
                            height: $xxs;
                            width: 0;
                            position: absolute;
                            bottom: $xxs;
                            left: 50%;
                            transform: translateX(-50%);
                            transition: width $transition;
                            border-radius: $xs;
                        }
    
                        &.active::after{
                            width: calc(100% - #{$xxl});
                        }
    
                        &:hover:not(.active)::after{
                            width: $l;
                        }
                    }
                }

                a[class^="btn"]{
                    color: white;
                }
            }
        }
    }

    // Mobile Nav
    &#mobile-nav{
        display: none;
        padding: $xs;

        #open-menu-btn,
        #close-menu-btn{
            color: var(--text-color);
        }
    
        #mobile-menu{
            height: 100vh;
            width: 100%;
            background-color: var(--foreground-color-1);
            position: fixed;
            top: 0;
            left: -100%;
            z-index: 999;
            display: grid;
            gap: $xl;
            padding: $xxxl 0;
            will-change: left;
            transition: left $transition;
    
            &.open{
                left: 0;
            }
    
            li{
                text-align: center;
                align-self: center;
    
                a{
                    text-decoration: none;
                }
            }
        }
    }
}

@media (prefers-color-scheme: light) {
    nav:not(#footer-nav){
        background-color: rgba($white, .8);
    }
}   
  
@media (prefers-color-scheme: dark) {
    nav:not(#footer-nav){
        background-color: rgba($dark-gray, .8);
    }
}

@media screen and (max-width: 960px){
    nav#primary-nav{
        display: none;
    }

    nav#mobile-nav{
        display: block !important;
    }
}