@import "variables";

/* -------------------- Colors -------------------- */

@each $name, $color in $colors {
    .bg-#{$name} {
        background-color: $color !important;
    }

    .text-#{$name} {
        color: $color !important;
    }
}

@each $name, $gradient in $gradients {
    .bg-#{$name} {
        background-image: $gradient !important;
    }

    .text-#{$name} {
        background-image: $gradient;
        background-clip: text;
        -webkit-text-fill-color: transparent;
    }
}

.rainbow-border{
    border: $xs solid transparent;
    border-image: $akq-gradient;
    border-image-slice: 1;
}

.theme-border{
    border: $xs solid transparent;
    border-image: $theme;
    border-image-slice: 1;
}
/* -------------------- Spacing -------------------- */

@each $name, $xsize in $xsizes {
    // margin

    .m-#{$name} {
        margin: $xsize !important;
    }

    .mt-#{$name} {
        margin-top: $xsize !important;
    }

    .mb-#{$name} {
        margin-bottom: $xsize !important;
    }

    .ml-#{$name} {
        margin-left: $xsize !important;
    }

    .mr-#{$name} {
        margin-right: $xsize !important;
    }

    .my-#{$name} {
       @extend .mt-#{$name};
       @extend .mb-#{$name}; 
    }

    // padding

    .p-#{$name} {
        padding: $xsize !important;
    }

    .px-#{$name} {
        @extend .pl-#{$name};
        @extend .pr-#{$name};
    }

    .py-#{$name} {
        @extend .pt-#{$name};
        @extend .pb-#{$name};
    }

    .pt-#{$name} {
        padding-top: $xsize !important;
    }

    .pb-#{$name} {
        padding-bottom: $xsize !important;
    }

    .pl-#{$name} {
        padding-left: $xsize !important;
    }

    .pr-#{$name} {
        padding-right: $xsize !important;
    }

    // gap

    .gap-#{$name} {
        gap: $xsize;
    }
}

.mx-auto{
    margin-left: auto !important;
    margin-right: auto !important;
}

/* -------------------- Layout -------------------- */

.w-50{
    max-width: 50% !important;
}

.container{
    width: 100%;
    max-width: 1024px;
    margin: 0 auto;

    &--wide{
        @extend .container;
        max-width: 1280px;
    }

    &--narrow{
        @extend .container;
        max-width: 960px;
    }
}

.grid{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax($xxxl, 1fr));
}

.flex{
    display: flex;

    &--column{
        @extend .flex;
        flex-direction: column;
    }

    &--responsive{
        @extend .flex;
    }

    &.wrap{
        flex-wrap: wrap;
    }
}

.align-start{
    align-items: flex-start;
}

.align-center{
    align-items: center;
}

.align-end{
    align-items: end;
}

.align-stretch{
    align-items: stretch;
}

.justify-center{
    justify-content: center;
}

.justify-end{
    justify-content: end;
    justify-items: end;
}

.space-between{
    justify-content: space-between;
}

.space-around{
    justify-content: space-around;
}

.space-evenly{
    justify-content: space-evenly;
}

@media screen and (max-width: 960px){
    .grid{
        grid-template-columns: 1fr;
        grid-template-rows: repeat(auto-fit, minmax(0px, 1fr));
    }

    .flex--responsive{
        flex-direction: column;
    }
}

.float-left{
    float: left;
}

.float-right{
    float: right;
}

/* -------------------- Text -------------------- */

.text-left{
    text-align: left !important;
}

.text-right{
    text-align: right !important;
}

.text-center{
    text-align: center !important;
}
