@import "../../stylesheets/partials/variables";

ul#project-list{

    li.card{
        gap: $xxl;

        .project-description{
            position: relative;
            z-index: 1;
        }

        .project-image{
            position: relative;
            max-height: 2*$xxxxl;
            width: 80%;

            svg{
                position: relative;
                z-index: 1;
                filter: drop-shadow(0 0 $l rgba($black, .4));
            }

            &::after{
                content: "";
                display: inline-block;
                width: 200%;
                height: auto;
                aspect-ratio: 1;
                background-image: $theme;
                mask-image: url("../../images/masks/blob-image-2.svg");
                mask-size: 100%;
                mask-repeat: no-repeat;
                mask-position: center;
                position: absolute;
                top: -50%;
                right: -100%;
                z-index: 0;
                transform-style: preserve-3d;
            }
        }

        &:nth-child(even){
            flex-direction: row-reverse;

            .project-image::after{
                right: 0;
            }
        }
    }
}

@media screen and (max-width: 960px){
    ul#project-list li.card{
        flex-direction: column !important;

        .project-image{
            width: 100%;
            order: -1;
            overflow: hidden;

            &::after{
                display: none;
            }

            svg{
                filter: none;
            }
        }
    }
}